body {
  min-width: 320px;
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  color: #191919;
  background-color: #fafafa;
  box-sizing: border-box;
  font-size: 10px;
}
h1 {
  font-size: 18px;
  font-family: 'PT Serif Caption', arial;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3rem;
  }
}
h2 {
  font-size: 26px;
  font-family: 'PT Serif Caption', arial;
}
@media (min-width: 768px) {
  h2 {
    font-size: 30px;
  }
}
h3 {
  font-size: 14px;
  font-family: 'Fira Sans', sans-serif;
  text-transform: uppercase;
}
p {
  font-size: 16px;
}
a {
  text-decoration: none;
  text-align: center;
  color: #191919;
}
.highlighted {
  color: #ff7435;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.page-header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.page-header__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  flex-wrap: wrap;
  position: relative;
}
.page-header__burger {
  display: block;
  position: absolute;
  right: 20px;
  top: 30px;
  z-index: 1;
}
@media (min-width: 1200px) {
  .page-header__burger {
    display: none;
  }
}
.page-header__contacts {
  display: none;
  font-size: 14px;
}
@media (min-width: 1200px) {
  .page-header__contacts {
    display: block;
    flex-basis: 25%;
  }
}
.page-header__phone {
  font-family: 'PT Sans Caption', sans-serif;
  font-size: 18px;
  font-weight: bold;
}
.page-header__btn {
  display: none;
}
@media (min-width: 1200px) {
  .page-header__btn {
    display: block;
  }
}
.logo {
  border: 1px solid #ebebeb;
  padding: 20px;
  flex-grow: 1;
  flex-basis: 100%;
  display: flex;
  justify-content: center;
  width: 145px;
}
@media (min-width: 1200px) {
  .logo {
    flex-grow: 0;
    border: none;
    flex-basis: auto;
  }
}
.main-nav {
  font-size: 14px;
  text-transform: uppercase;
  flex-basis: 100%;
  display: none;
}
.main-nav--open {
  display: block;
}
@media (min-width: 1200px) {
  .main-nav {
    display: block;
    flex-basis: 41.667%;
  }
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 1200px) {
  .main-nav__list {
    padding: 0 10px;
  }
}
.main-nav__item {
  text-align: center;
  padding: 10px;
  border: 1px solid #ebebeb;
}
.main-nav__item:hover {
  color: #ff7435;
}
.main-nav__item--active {
  color: #ff7435;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main-nav__item--active:after {
  content: "";
  width: 30px;
  height: 2px;
  background-color: #ff7435;
  display: block;
  margin-top: 3px ;
}
@media (min-width: 1200px) {
  .main-nav__item {
    border: none;
    display: inline-block;
  }
}
.btn {
  padding: 10px 20px;
  border-radius: 20px;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-family: 'Fira Sans', sans-serif;
}
.btn--orange {
  background-color: #ff7435;
}
.btn--orange:hover {
  background-color: #e85715;
}
.btn--picture:before {
  content: "";
  background-image: url(../img/calendar-btn-pic.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 15px;
  height: 15px;
  display: inline-block;
  vertical-align: middle;
  margin: 5px;
}
.btn--green {
  background-color: #0ab89d;
  border-radius: 0;
}
.btn--green:hover {
  background-color: #079b84;
}
.btn--transparent {
  background-color: transparent;
  border: 2px solid #fff;
}
.btn--transparent:hover {
  background-color: #fafafa;
  -webkit-text-fill-color: transparent;
}
.btn--more {
  color: #191919;
  text-decoration: underline;
  text-decoration-style: dotted;
  background-color: #fafafa;
}
.btn--more:hover {
  text-decoration: none;
}
.promo-slider {
  border: 1px solid #000;
  min-height: 320px;
}
.promo-slider__item {
  background-image: url(img/promo-family.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left;
  border: 1px solid blue;
  min-height: 320px;
}
.promo-slider__text {
  width: 100%;
  margin-top: 20px;
}
@media (min-width: 768px) {
  .promo-slider__text {
    margin: 20px;
    text-align: left;
    width: 75%;
  }
}
@media (min-width: 1200px) {
  .promo-slider__text {
    width: 50%;
  }
}
.promo-slider__title,
.promo-slider__info {
  background-color: rgba(250, 250, 250, 0.95);
  margin: 0;
  padding: 10px;
}
@media (min-width: 768px) {
  .promo-slider__title,
  .promo-slider__info {
    margin-bottom: 20px;
  }
}
.tabs {
  display: flex;
  flex-basis: 100%;
  justify-content: center;
}
.tabs__item {
  margin: 20px;
  font-size: 14px;
  text-transform: uppercase;
  color: #959595;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tabs__item--active {
  color: #ff7435;
}
.tabs__item--active:after {
  content: "";
  width: 30px;
  height: 3px;
  background-color: #ff7435;
  display: block;
  margin-top: 5px ;
}
.health-block {
  display: flex;
  justify-content: center;
  border: 1px solid red;
  background-image: url(/img/health-promo-bgr.png);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}
.health-block__inner {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-basis: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .health-block__inner {
    flex-basis: 75%;
  }
}
@media (min-width: 1200px) {
  .health-block__inner {
    flex-basis: 66.67%;
  }
}
.health-block__activities {
  display: none;
}
@media (min-width: 768px) {
  .health-block__activities {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-basis: 100%;
  }
}
.health-block__item {
  flex-basis: 25%;
}
@media (min-width: 1200px) {
  .health-block__item {
    flex-basis: 16%;
  }
}
@media (min-width: 1200px) {
  .gallery__inner {
    margin: 20px 100px 40px 100px;
  }
}
.gallery__img {
  width: 100%;
  height: auto;
  display: none;
}
@media (min-width: 768px) {
  .gallery__img {
    display: block;
  }
}
.testimonials {
  background-image: url(/img/testimonials-bgr.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  margin: 20px 0;
}
.testimonials__inner {
  text-align: center;
  width: 100%;
  padding: 50px 0;
}
.testimonials__title {
  color: #fff;
  margin-bottom: 50px;
}
.testimonials__slider {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.testimonials__btn {
  margin: 40px 0;
}
.review {
  background-color: #fafafa;
  flex-basis: 100%;
  text-align: center;
  display: none;
  padding-bottom: 10px;
  margin-bottom: 50px;
}
@media (min-width: 768px) {
  .review {
    flex-basis: 60%;
  }
  .review:nth-child(2) {
    display: block;
  }
}
@media (min-width: 1200px) {
  .review {
    flex-basis: 40%;
    margin-left: 20px;
    margin-right: 20px;
  }
}
.review__caption {
  font-weight: bold;
}
.review__img {
  margin-top: -20px;
}
.review__text {
  padding: 20px;
  margin: auto;
}
.review__dates {
  font-size: 14px;
}
.review:first-child {
  display: block;
}
.offers {
  text-align: center;
  position: relative;
}
@media (min-width: 1200px) {
  .offers__inner {
    margin: 0 100px;
  }
}
.offers__infoblock-title {
  color: #ff7435;
  text-align: left;
}
.offers__infoblock {
  text-align: left;
  padding: 10px;
  width: 90%;
  position: absolute;
  left: 0;
  bottom: 25px;
  background-color: rgba(250, 250, 250, 0.95);
}
@media (min-width: 768px) {
  .offers__infoblock {
    width: 35%;
  }
}
.offers__img {
  width: 100%;
}
.offers__slider .owl-dots {
  text-align: center;
}
.offers__slider .owl-dot {
  width: 15px;
  height: 15px;
  display: inline-block;
  border: 3px solid #ff7435;
  border-radius: 50%;
  margin: 15px;
}
.offers__slider .active {
  background-color: #ff7435;
}
.transport {
  text-align: center;
}
@media (min-width: 1200px) {
  .transport__inner {
    margin: 20px 100px 40px 100px;
  }
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
